import TicketButton from '../elements/Buttons/Ticket';
import styles from './Order.module.scss';

export default function Order(props) {
  return (
    <div className={styles.Order} id='order'>
      <div className={styles.Head}>
        <h2 className={styles.Head__title}>  </h2>
        <h3 className={styles.Head__subtitle}>   </h3>
      </div>
      <form action="" method="post" className={styles.Form}>
        <input
          type="text"
          placeholder=""
          className={styles.Form__input}/>
        <input
          type="text"
          placeholder=""
          className={styles.Form__input}/>
        <input
          type="text"
          placeholder=""
          className={styles.Form__input}/>
        <input
          type="email"
          placeholder=" "
          className={styles.Form__input}/>
        <input
          type="datetime-local"
          placeholder="   "
          className={styles.Form__input}/>
        <textarea
          name="" id=""
          placeholder="  "
          cols="30" rows="50"
          className={`${styles.Form__input} ${styles.Form__input_textarea}`}
        ></textarea>
        <TicketButton className={styles.Form__submit} value=" " type="submit" />
      </form>
    </div>
  );
}