import React from 'react';

import Navbar from '../Navbar';
import TicketButton from '../elements/Buttons/Ticket';

import styles from './Main.module.scss';

export default function Main({ size, menu, setSelectedTab, selectedTab}) {
  function returnText() {
    if (size.width > 1280) {
      return (
        <p className={styles.Ad__text}>
             .    ,
          <br />
                  ,   
          <br />
             .  ,     
          <br />
            .
        </p>
      );
    } if (size.width > 688) {
      return (
        <p className={styles.Ad__text}>
             .   
          <br />
          ,         ,
          <br />
                .  ,
          <br />
                 .
        </p>
      );
    } if (size.width > 0) {
      return (
        <p className={styles.Ad__text}>
             . 
          <br />
            ,  
          <br />
                ,
          <br />
                .
          <br />
           ,      
          <br />
           .
        </p>
      );
    }

    return '';
  }

  return (
    <div className={styles.Main}>
      <Navbar menu={menu} selectedTab={selectedTab} setSelectedTab={setSelectedTab}/>
      <div className={styles.Ad}>
        <h1 className={styles.Ad__title}>
             
          <br />
          <span> 1700/</span>
        </h1>
        <h3 className={styles.Ad__info}> </h3>
        { returnText() }
        <TicketButton className={styles.Ad__TicketButton} value=" " />
      </div>
      <div className={styles.Cake} />

    </div>
  );
}
